    <h1 class="title" id="abs">Abs</h1>
    <section class="content">
      <h3>Plank</h3>
      <img src="../assets/learn/plank.jpg" />
      <p>
        Get into position with your elbows under your shoulders, your feet
        together and your hips raised with abs and glutes engaged so your body
        forms a straight line from head to heels. Hold this position without
        letting your hips drop.
      </p>
    </section>

    <section class="content">
      <h3>Crunch</h3>
      <img src="../assets/learn/crunch.jpg" />
      <p>
        Lie flat on your back with your knees bent and hands by your temples or
        crossed over your chest. Engage your upper abs to raise your torso, then
        crunch your upper body up towards your knees. Lower slowly, keeping
        tension on your abs throughout.
      </p>
    </section>

    <h1 class="title" id="arms">Arms</h1>
    <section class="content">
      <h3>Standing Dumbell Biceps Curl</h3>
      <img src="../assets/learn/standing-dumbbell-biceps-curl.jpg" />
      <p>
        Stand tall, holding a dumbbell in each hand with your palms facing
        forwards. Keeping your elbows tight to your sides, curl the weights up
        to shoulder height. Squeeze your biceps at the top, then lower the
        weights back to the start.
      </p>
    </section>
    <section class="content">
      <h3>Dumbell Triceps Extension</h3>
      <img src="../assets/learn/dumbbell-triceps-extension.jpg" />
      <p>
        Stand tall, holding a dumbbell over your head with your arm straight.
        Keeping your elbow pointing to the ceiling, lower the weight behind your
        head, then straighten your arm to return to the start. Repeat for all
        the reps, then switch arms.
      </p>
    </section>

    <h1 class="title" id="back">Back</h1>
    <section class="content">
      <h3>Wide Lat Pull-down</h3>
      <img src="../assets/learn/wide-lat-pull-down.jpg" />
      <p>
        Position yourself at the machine with a double shoulder-width overhand
        grip on the bar. Keeping your chest up and abs braced, pull the bar
        down, leading with your elbows. Hold the bottom position for a second,
        then return to the start.
      </p>
    </section>
    <section class="content">
      <h3>Seated Cable Row</h3>
      <img src="../assets/learn/seated-cable-row.jpg" />
      <p>
        Sit on the machine, holding a double-grip cable attachment in both
        hands. Keeping your chest up, row your hands in towards your body,
        leading with your elbows. Pause at the top position, then return to the
        start.
      </p>
    </section>

    <h1 class="title" id="chest">Chest</h1>
    <section class="content">
      <h3>Bench Press</h3>
      <img src="../assets/learn/bench-press.jpg" />
      <p>
        Lie flat on a bench, holding a bar with a shoulder-width grip. Plant
        your feet on the floor and tense your muscles. Lower the bar until it
        touches your chest, then press it back up powerfully.
      </p>
    </section>
    <section class="content">
      <h3>Incline Dumbell Flye</h3>
      <img src="../assets/learn/incline-dumbbell-flye.jpg" />
      <p>
        Lie back on an incline bench, holding two dumbbells directly over your
        chest with straight arms. Bend your elbows slightly, then lower your
        hands out to the sides until you feel a stretch across your chest.
        Squeeze your pecs to return to the start.
      </p>
    </section>

    <h1 class="title" id="legs">Legs</h1>
    <section class="content">
      <h3>Back Squat</h3>
      <img src="../assets/learn/back-squat.jpg" />
      <p>
        Stand tall, holding a bar across the back of your shoulders. Keeping
        your chest up and your whole body tight, bend your knees to squat down
        as low as you can but don’t let your knees roll inwards. Push through
        your heels to stand back up.
      </p>
    </section>
    <section class="content">
      <h3>Leg Extensions</h3>
      <img src="../assets/learn/leg-extension.jpg" />
      <p>
        Position yourself correctly on the machine with the padded bar against
        the bottom of your shins. Keeping your upper body tight, raise your feet
        to straighten your legs. Pause at the top with your quads engaged, then
        lower back to the start.
      </p>
    </section>
